/* global CustomFunctions */

import priceTargetConsensusLoader from "../dataloaders/priceTargetConsensus";
import { PRICE_TARGET_CONSENSUS_KEYS, PriceTargetConsensusKey, PriceTargetConsensusKeyEnum } from "./types";

/**
 * Returns the value of the specified price target consensus key for the given stock symbol.
 * @customfunction PRICE_TARGET_CONSENSUS
 * @volatile
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The value of the specified price target consensus key
 */
export async function priceTargetConsensus(key: string, stockSymbol: string): Promise<number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!PRICE_TARGET_CONSENSUS_KEYS.includes(key as PriceTargetConsensusKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const priceTargetConsensusData = await priceTargetConsensusLoader.load(stockSymbol);
  if (!priceTargetConsensusData) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${stockSymbol}`);
  }
  return priceTargetConsensusData[key];
}

/**
 * Returns the target high price for the given stock symbol.
 * @customfunction PRICE_TARGET_CONSENSUS.TARGET_HIGH
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The target high price of the stock
 */
export async function priceTargetConsensusTargetHigh(stockSymbol: string): Promise<number> {
  return priceTargetConsensus(PriceTargetConsensusKeyEnum.TargetHigh, stockSymbol);
}

/**
 * Returns the target low price for the given stock symbol.
 * @customfunction PRICE_TARGET_CONSENSUS.TARGET_LOW
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The target low price of the stock
 */
export async function priceTargetConsensusTargetLow(stockSymbol: string): Promise<number> {
  return priceTargetConsensus(PriceTargetConsensusKeyEnum.TargetLow, stockSymbol);
}

/**
 * Returns the target consensus price for the given stock symbol.
 * @customfunction PRICE_TARGET_CONSENSUS.TARGET_CONSENSUS
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The target consensus price of the stock
 */
export async function priceTargetConsensusTargetConsensus(stockSymbol: string): Promise<number> {
  return priceTargetConsensus(PriceTargetConsensusKeyEnum.TargetConsensus, stockSymbol);
}

/**
 * Returns the target median price for the given stock symbol.
 * @customfunction PRICE_TARGET_CONSENSUS.TARGET_MEDIAN
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The target median price of the stock
 */
export async function priceTargetConsensusTargetMedian(stockSymbol: string): Promise<number> {
  return priceTargetConsensus(PriceTargetConsensusKeyEnum.TargetMedian, stockSymbol);
}


CustomFunctions.associate("PRICE_TARGET_CONSENSUS", priceTargetConsensus);
CustomFunctions.associate("PRICE_TARGET_CONSENSUS.TARGET_HIGH", priceTargetConsensusTargetHigh);
CustomFunctions.associate("PRICE_TARGET_CONSENSUS.TARGET_LOW", priceTargetConsensusTargetLow);
CustomFunctions.associate("PRICE_TARGET_CONSENSUS.TARGET_CONSENSUS", priceTargetConsensusTargetConsensus);
CustomFunctions.associate("PRICE_TARGET_CONSENSUS.TARGET_MEDIAN", priceTargetConsensusTargetMedian);