/* global CustomFunctions */

import ratiosLoader from "../dataloaders/ratios";
import { RATIOS_KEYS, RatiosKey, RatiosKeyEnum } from "./types";

/**
 * Returns the value of the specified ratios key for the given stock symbol.
 * @customfunction RATIOS
 * @volatile
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The value of the specified ratios key
 */
export async function ratios(key: string, stockSymbol: string): Promise<number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!RATIOS_KEYS.includes(key as RatiosKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const ratiosData = await ratiosLoader.load(stockSymbol);
  if (!ratiosData) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${stockSymbol}`);
  }
  return ratiosData[key];
}

