/* global CustomFunctions */

import cashFlowStatementLoader from "../dataloaders/cashFlowStatement";
import { CASH_FLOW_STATEMENT_KEYS, CashFlowStatementKey, CashFlowStatementKeyEnum } from "./types";

/**
 * Returns the value of the specified cash flow statement key for the given stock symbol.
 * @customfunction CASH_FLOW_STATEMENT
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @param {number} year The year to fetch data for
 * @param {number} [quarter] The quarter to fetch data for, default 0 for full year, 1 for Q1, 2 for Q2, 3 for Q3, 4 for Q4
 * @returns {Promise<number>} The value of the specified cash flow statement key
 */
export async function cashFlowStatement(
  key: string,
  stockSymbol: string,
  year: number,
  quarter: number = 0
): Promise<number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!year) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Year is required");
  }
  if (!CASH_FLOW_STATEMENT_KEYS.includes(key as CashFlowStatementKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const cashFlowStatementData = await cashFlowStatementLoader.load({ symbol: stockSymbol, year, quarter });
  if (!cashFlowStatementData) {
    const quarterText = quarter ? `Q${quarter}` : "Full Year";
    throw new CustomFunctions.Error(
      CustomFunctions.ErrorCode.notAvailable,
      `Record Not found: ${stockSymbol} ${year} ${quarterText}`
    );
  }
  return cashFlowStatementData[key];
}

