/* global CustomFunctions */

import quoteLoader from "../dataloaders/quote";
import { QUOTE_KEYS, QuoteKey, QuoteKeyEnum } from "./types";

/**
 * Returns the value of the specified quote key for the given stock symbol.
 * @customfunction QUOTE
 * @volatile
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The value of the specified quote key
 */
export async function quote(key: string, stockSymbol: string): Promise<number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!QUOTE_KEYS.includes(key as QuoteKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const quote = await quoteLoader.load(stockSymbol);
  if (!quote) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${stockSymbol}`);
  }
  return quote[key];
}

/**
 * Returns the price for the given stock symbol.
 * @customfunction QUOTE.PRICE
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The price of the stock
 */
export async function quotePrice(stockSymbol: string): Promise<number> {
  return quote(QuoteKeyEnum.Price, stockSymbol);
}

