import DataLoader from "dataloader";
import { getFromLocalStorage } from "../taskpane/taskpane";

type IncomeStatementInput = {
  symbol: string;
  year?: number;
  quarter?: number;
};

async function batchGetIncomeStatement(inputs: IncomeStatementInput[]) {
  const apiKey = getFromLocalStorage("FmpApiKey");
  const symbolsRequest = {};
  const results = {};
  inputs.forEach((input) => {
    if (!symbolsRequest[input.symbol]) {
      symbolsRequest[input.symbol] = {};
    }
    const peroid = input.quarter ? `quarter` : "annual";
    if (!symbolsRequest[input.symbol][peroid]) {
      symbolsRequest[input.symbol][peroid] = [];
    }
    symbolsRequest[input.symbol][peroid].push({ year: input.year, quarter: input.quarter });
  });
  await Promise.all(
    Object.keys(symbolsRequest).map(async (symbol) => {
      await Promise.all(
        Object.keys(symbolsRequest[symbol]).map(async (peroid) => {
          const url = `https://financialmodelingprep.com/api/v3/income-statement/${symbol}?period=${peroid}&apikey=${apiKey}`;
          const response = await fetch(url);
          if (!response.ok) {
            return null;
          }
          const data = await response.json();
          symbolsRequest[symbol][peroid].forEach(({ year, quarter }) => {
            const quarterStr = quarter ? `Q${quarter}` : "FY";
            const res = data.find(
              (statement) => statement.calendarYear === year.toString() && statement.period === quarterStr
            );
            const key = `${symbol}_${year}_${quarterStr}`;
            results[key] = res;
          });
        })
      );
    })
  );

  return inputs.map(({ symbol, year, quarter }) => {
    const quarterStr = quarter ? `Q${quarter}` : "FY";
    const key = `${symbol}_${year}_${quarterStr}`;
    return key ? results[key] : null;
  });
}

export default new DataLoader((inputs: IncomeStatementInput[]) => batchGetIncomeStatement(inputs));
