/* global CustomFunctions */

import incomeStatementLoader from "../dataloaders/incomeStatement";
import { INCOME_STATEMENT_KEYS, IncomeStatementKey, IncomeStatementKeyEnum } from "./types";

/**
 * Returns the value of the specified income statement key for the given stock symbol.
 * @customfunction INCOME_STATEMENT
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @param {number} year The year to fetch data for
 * @param {number} [quarter] The quarter to fetch data for, default 0 for full year, 1 for Q1, 2 for Q2, 3 for Q3, 4 for Q4
 * @returns {Promise<number>} The value of the specified income statement key
 */
export async function incomeStatement(
  key: string,
  stockSymbol: string,
  year: number,
  quarter?: number
): Promise<number> {
  if (!INCOME_STATEMENT_KEYS.includes(key as IncomeStatementKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }
  if (quarter && (quarter < 0 || quarter > 4)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid quarter: ${quarter}`);
  }

  const incomeStatement = await incomeStatementLoader.load({ symbol: stockSymbol, year, quarter });
  if (!incomeStatement) {
    const quarterText = quarter ? `Q${quarter}` : "Full Year";
    throw new CustomFunctions.Error(
      CustomFunctions.ErrorCode.notAvailable,
      `Record Not found: ${stockSymbol} ${year} ${quarterText}`
    );
  }
  return incomeStatement[key];
}

