/* global CustomFunctions */

import companyProfileLoader from "../dataloaders/companyProfile";
import "@azure/openai/types";
import { AzureOpenAI } from "openai";
import { getFromLocalStorage } from "../taskpane/taskpane";

async function getCompanyData(stockSymbol: string): Promise<Record<string, any>> {
  const profile = await companyProfileLoader.load(stockSymbol);
  if (!profile) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${stockSymbol}`);
  }
  return profile;
}

/**
 * Screens stocks based on given criteria.
 * @customfunction SCREENING
 * @param {string} query The screening criteria
 * @param {number} [direction] The direction to output the results, 0 for horizontal, 1 for vertical, default 0
 * @returns {Promise<string[][]>} An array of stock symbols that match the criteria
 */
export async function screening(query: string, direction = 0): Promise<string[][]> {
  try {
    const apiKey = getFromLocalStorage("GPTKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const client = new AzureOpenAI({
      baseURL: "https://apoidea-chatgpt-api.openai.azure.com/openai/deployments/gpt4o-mini-deployment/",
      apiVersion: "2024-05-01-preview",
      apiKey,
      deployment: "gpt4o-mini-deployment",
      dangerouslyAllowBrowser: true,
    });

    const response = await client.chat.completions.create({
      model: "gpt-4o-mini",
      messages: [
        {
          role: "system",
          content: `You are a helpful assistant.
You must only respond in JSON format that adheres to the following schema:
{
"list": list[]
}

- Based on the query, provide a list of stock codes that is the answer to the query,，丁冐戶吐C
- for US Stock, the stock code is the ticker symbol
- for Stock other than US, append the exchange code to the ticker symbol, eg 700.HK
- If the query is not related to any listed company, put 'NA' in "list"`,
        },
        {
          role: "user",
          content: `query: ${query}`,
        },
      ],
    });

    const content = response.choices[0].message.content;
    if (!content) {
      throw new Error("No content in response");
    }

    const parsedContent = JSON.parse(content);
    const resultList = parsedContent.list;
    if (direction === 0) {
      return [resultList];
    } else {
      return resultList.map((item) => [item]);
    }
  } catch (error) {
    console.error(`Error in SCREENING function:`, error);
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Error in SCREENING function");
  }
}

/**
 * Answers questions about a company based on its stock symbol.
 * @customfunction ANSWER
 * @param {string} stockSymbol The stock symbol of the company
 * @param {string} query The question to ask about the company
 * @returns {Promise<string>} The answer to the query
 */
export async function answer(stockSymbol: string, query: string): Promise<string> {
  try {
    const companyData = await getCompanyData(stockSymbol);
    const requestBody = JSON.stringify({
      query: `Company: ${companyData.companyName}\nstockcode: ${stockSymbol}\nQuery: ${query}\n Instruction: return the shortest answer possible`,
    });

    const response = await fetch("https://stockchat-intelligence-service.arbor.news/poc/query/answer", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: requestBody,
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();

    if (data.answer) {
      return data.answer;
    } else {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "No answer field in response");
    }
  } catch (error) {
    console.error(`Error in ANSWER function:`, error);
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, error.message);
  }
}

/**
 * Answers factual questions about a company based on its stock symbol.
 * @customfunction ANSWER_FACTUAL
 * @param {string} stockSymbol The stock symbol of the company
 * @param {string} query The factual question to ask about the company
 * @returns {Promise<string>} The factual answer to the query
 */
export async function answerFactual(stockSymbol: string, query: string): Promise<string> {
  try {
    const companyData = await getCompanyData(stockSymbol);
    const apiKey = getFromLocalStorage("GPTKey");
    if (!apiKey) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API key not found in settings");
    }

    const client = new AzureOpenAI({
      baseURL: "https://apoidea-chatgpt-api.openai.azure.com/openai/deployments/gpt4o-mini-deployment/",
      apiVersion: "2024-05-01-preview",
      apiKey,
      deployment: "gpt4o-mini-deployment",
      dangerouslyAllowBrowser: true,
    });

    const response = await client.chat.completions.create({
      model: "gpt4o-mini-deployment",
      messages: [
        {
          role: "system",
          content: `You are a helpful assistant providing factual information about companies. Answer the query concisely with an elliptical response (aka short answer) based on the given company and stock code. Output in JSON: {"answer": string}`,
        },
        {
          role: "user",
          content: `Company: ${companyData.companyName}\nStock Code: ${stockSymbol}\n Question:${query}`,
        },
      ],
      max_tokens: 50,
      temperature: 0.9,
    });
    // if (response.status === 401) {
    //   throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
    // }

    const json = JSON.parse(response.choices[0].message.content.trim());

    return json["answer"] || "";
  } catch (error) {
    if (error.status === 401) {
      throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "API Key not valid");
    }
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, "Unknown error in ANSWER_FACTUAL function");
  }
}

CustomFunctions.associate("SCREENING", screening);
CustomFunctions.associate("ANSWER", answer);
CustomFunctions.associate("ANSWER_FACTUAL", answerFactual);