/* global CustomFunctions */

import keyMetricsLoader from "../dataloaders/keyMetrics";
import { KEY_METRICS_KEYS, KeyMetricsKey, KeyMetricsKeyEnum } from "./types";

/**
 * Returns the value of the specified key metrics key for the given stock symbol.
 * @customfunction KEY_METRICS
 * @volatile
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<number>} The value of the specified key metrics key
 */
export async function keyMetrics(key: string, stockSymbol: string): Promise<number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!KEY_METRICS_KEYS.includes(key as KeyMetricsKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const keyMetricsData = await keyMetricsLoader.load(stockSymbol);
  if (!keyMetricsData) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Record Not found: ${stockSymbol}`);
  }
  return keyMetricsData[key];
}

