/* global localStorage */
export async function insertText(text: string) {
  // Write text to the top left cell.
  try {
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const range = sheet.getRange("A1");
      range.values = [[text]];
      range.format.autofitColumns();
      await context.sync();
    });
  } catch (error) {
    console.log("Error: " + error);
  }
}

export async function insertCriteria(criteria: string) {
  try {
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const range = sheet.getRange("A1");
      range.values = [[criteria]];
      await context.sync();
    });
  } catch (error) {
    console.error("Error inserting criteria:", error);
  }
}

export async function getPasteRange(): Promise<{ address: string; direction: "down" | "right"; limit: number | null }> {
  try {
    const result = await Excel.run(
      async (context): Promise<{ address: string; direction: "down" | "right"; limit: number | null }> => {
        const selectedRange = context.workbook.getSelectedRange();
        selectedRange.load("rowCount");
        selectedRange.load("columnCount");
        const cell = selectedRange.getCell(0, 0);
        cell.load("address");
        await context.sync();
        const xCount = selectedRange.columnCount;
        const yCount = selectedRange.rowCount;
        const address = cell.address;
        return {
          address: address,
          direction: xCount === 1 ? "down" : yCount > 1 ? "down" : "right",
          limit:
            xCount === 1 && yCount === 1
              ? null
              : xCount === 1 && yCount > 1
                ? yCount
                : xCount > 1 && yCount === 1
                  ? xCount
                  : null,
        };
      }
    );
    return result;
  } catch (error) {
    console.error("Error getting paste range:", error);
    // Paste error message to cell A1
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const cellA1 = sheet.getRange("A1");
      cellA1.values = [[error.toString()]];
      await context.sync();
    });
    throw error; // Re-throw the error to allow the caller to handle it
  }
}

export async function pasteData(address: string, direction: "down" | "right", values: string[][]) {
  try {
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const range = sheet.getRange(address);
      await context.sync();
      if (direction === "down") {
        const resizedRange = range.getResizedRange(values.length - 1, values[0].length - 1);
        resizedRange.values = values;
      } else {
        values = transposeArray(values);
        const resizedRange = range.getResizedRange(values.length - 1, values[0].length - 1);
        resizedRange.values = values;
      }
      await context.sync();
    });
  } catch (error) {
    console.error("Error getting paste range:", error);
    // Paste error message to cell A1
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const cellA1 = sheet.getRange("A1");
      cellA1.values = [[error.toString()]];
      await context.sync();
    });
    throw error; // Re-throw the error to allow the caller to handle it
  }
}

function transposeArray(array: string[][]): string[][] {
  return array[0].map((_, colIndex) => array.map((row) => row[colIndex]));
}

export function setInLocalStorage(key: string, value: string) {
  const myPartitionKey = Office.context.partitionKey;

  // Check if local storage is partitioned.
  // If so, use the partition to ensure the data is only accessible by your add-in.
  if (myPartitionKey) {
    localStorage.setItem(myPartitionKey + key, value);
  } else {
    localStorage.setItem(key, value);
  }
}

export function getFromLocalStorage(key: string) {
  const myPartitionKey = Office.context.partitionKey;

  // Check if local storage is partitioned.
  if (myPartitionKey) {
    return localStorage.getItem(myPartitionKey + key);
  } else {
    return localStorage.getItem(key);
  }
}