import DataLoader from "dataloader";
import { uniq, keyBy } from "lodash";
import { getFromLocalStorage } from "../taskpane/taskpane";

async function batchGetPriceTargetConsensus(symbols: string[]) {
  const apiKey = getFromLocalStorage("FmpApiKey");
  const symbolsUniq = uniq(symbols);
  const results = await Promise.all(
    symbolsUniq.map(async (symbol) => {
      const url = `https://financialmodelingprep.com/api/v4/price-target-consensus?symbol=${symbol}&apikey=${apiKey}`;
      const response = await fetch(url);
      if (!response.ok) {
        return null;
      }
      const data = await response.json();
      return data.length > 0 ? data[0] : null;
    })
  );
  const keyBySymbol = keyBy(results, "symbol");

  return symbols.map((symbol) => keyBySymbol[symbol]);
}

export default new DataLoader((symbols: string[]) => batchGetPriceTargetConsensus(symbols));
