import DataLoader from "dataloader";
import { uniq, keyBy } from "lodash";
import { getFromLocalStorage } from "../taskpane/taskpane";

async function batchGetQuote(symbols: string[]) {
  const apiKey = getFromLocalStorage("FmpApiKey");
  const symbolsStr = uniq(symbols).join(",");
  const url = `https://financialmodelingprep.com/api/v3/quote/${symbolsStr}?apikey=${apiKey}`;
  let keyBySymbol = {};
  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    const data = await response.json();
    keyBySymbol = keyBy(data, "symbol");

    return symbols.map((symbol) => keyBySymbol[symbol]);
  } catch (error) {
    console.error("Error fetching stock data:", error);
  }

  return symbols.map((symbol) => keyBySymbol[symbol]);
}

export default new DataLoader((symbols: string[]) => batchGetQuote(symbols));
