/* global CustomFunctions */

import companyProfileLoader from "../dataloaders/companyProfile";
import { COMPANY_PROFILE_KEYS, CompanyProfileKey, CompanyProfileKeyEnum } from "./types";

/**
 * Returns the value of the specified company profile key for the given stock symbol.
 * @customfunction COMPANY_PROFILE
 * @volatile
 * @param {string} key The key to fetch the value for
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<string>} The value of the specified company profile key
 */
export async function companyProfile(key: string, stockSymbol: string): Promise<string | number> {
  if (!stockSymbol) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, "Stock symbol is required");
  }
  if (!COMPANY_PROFILE_KEYS.includes(key as CompanyProfileKey)) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidValue, `Invalid key: ${key}`);
  }

  const profile = await companyProfileLoader.load(stockSymbol);
  if (!profile) {
    throw new CustomFunctions.Error(CustomFunctions.ErrorCode.notAvailable, `Stock symbol not found: ${stockSymbol}`);
  }
  return profile[key];
}

/**
 * Returns the company name for the given stock symbol.
 * @customfunction COMPANY_PROFILE.COMPANY_NAME
 * @volatile
 * @param {string} stockSymbol The stock symbol to fetch data for
 * @returns {Promise<string>} The company name
 */
export async function companyProfileCompanyName(stockSymbol: string): Promise<string> {
  return companyProfile(CompanyProfileKeyEnum.CompanyName, stockSymbol) as Promise<string>;
}

