import DataLoader from "dataloader";
import { getFromLocalStorage } from "../taskpane/taskpane";

type BalanceSheetInput = {
  symbol: string;
  year?: number;
  quarter?: number;
};
async function batchGetBalanceSheet(inputs: BalanceSheetInput[]) {
  const apiKey = getFromLocalStorage("FmpApiKey");
  const symbolsRequest = {};
  const results = {};
  inputs.forEach((input) => {
    if (!symbolsRequest[input.symbol]) {
      symbolsRequest[input.symbol] = {};
    }
    const period = input.quarter ? `quarter` : "annual";
    if (!symbolsRequest[input.symbol][period]) {
      symbolsRequest[input.symbol][period] = [];
    }
    symbolsRequest[input.symbol][period].push({ year: input.year, quarter: input.quarter });
  });
  await Promise.all(
    Object.keys(symbolsRequest).map(async (symbol) => {
      await Promise.all(
        Object.keys(symbolsRequest[symbol]).map(async (period) => {
          const url = `https://financialmodelingprep.com/api/v3/balance-sheet-statement/${symbol}?period=${period}&apikey=${apiKey}`;
          const response = await fetch(url);
          if (!response.ok) {
            return null;
          }
          const data = await response.json();
          symbolsRequest[symbol][period].forEach(({ year, quarter }) => {
            const quarterStr = quarter ? `Q${quarter}` : "FY";
            const res = data.find(
              (statement) => statement.calendarYear === year.toString() && statement.period === quarterStr
            );
            const key = `${symbol}_${year}_${quarterStr}`;
            // Fix fmp typo
            results[key] = { ...res, otherTotalStockholdersEquity: res.othertotalStockholdersEquity };
          });
        })
      );
    })
  );

  return inputs.map(({ symbol, year, quarter }) => {
    const quarterStr = quarter ? `Q${quarter}` : "FY";
    const key = `${symbol}_${year}_${quarterStr}`;
    return key ? results[key] : null;
  });
}

export default new DataLoader((inputs: BalanceSheetInput[]) => batchGetBalanceSheet(inputs));
